body {
  margin: 0;
  padding: 0;
  font-family: 'Courgette', cursive;
  height: 100vh;
}
.dashboard-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  width: 100vw;
  padding-bottom: 50px;
}

.add-content {
  width: 100vw;
}

.add-content-wrapper {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.before-signin {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.after-signin {
  background-color: #EEF0FB;
  /* background-color: #5d6fd9; */
  display: flex;
  flex-direction: row;
}

.gradient {
  background-color: #473ecc;
  height: 100vh;
}

.navbar {
  min-width: 200px;
  border-radius: 0 50px 50px 0;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.5);
}

.navbar div {
  width: 100%;
  margin: 0;
}

/* Component- Modal */
.modal-content {
  width: 40vw;
  height: 25vh;
  background-color: rgba(0, 0, 0, 0);
  border: 0px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.profile-box {
  width: 35vw;
  height: 20vh;
}

.ngo-box {
  width: 35vw;
  height: 55vh;
  background-color: rgb(0, 0, 0);
  margin: 5px;
  overflow: scroll;

}

.employee-box {
  width: 35vw;
  height: 80vh;
  overflow: hidden;
  background-color: rgb(0, 0, 0);
  margin: 5px;
  overflow: scroll;
}

.box-common {
  background-color: #473ecc;
  margin: 5px;
  border-radius: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.add-box {
  width: 50vw;
  height: 60vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.profile-content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.profile-image {
  border-radius: 50%;
}

.profile-extras {
  font-size: 18px;
}

.ngo-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ngo-content h2 {
  overflow: hidden;
  position: fixed;
}

.ngo-content-div {
  margin-top: 50px;
  width: 100%;
}

.ngo-content-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #5951d1;
  color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  border-radius: 0;
  padding: 25px 10px;
}

.ngo-box-name {
  margin: 0;
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
}

.ngo-box-purpose {
  font-size: 12px;
  color: #C2C2C2;
  margin: 0;
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
}

.box-heading {
  color: #fff;
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
  margin-top: 5px;
}

.employee-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.employee-content h2 {
  overflow: hidden;
  position: fixed;
}

.employee-content-div {
  width: 100%;
  margin-top: 50px;
}

.employee-button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #5951d1;
  border: 0;
  padding: 20px 10px;
  color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  border-radius: 0;
  width: 100%;
}

.employee-button:hover {
  background-color: #5A54B6;
}

button:focus {outline:0;}

.employee-button h5 {
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
  margin: 5px;
}

.employee-button p {
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
  margin: 5px;
}

/* Component- Logo */
.signin-logo {
  font-family: "Courgette", cursive;
  font-size: 75px;
  color: #fff;
  text-shadow: -2px 0px 6px #121212;
}

.homepage-logo {
  font-family: "Courgette", cursive;
  font-size: 32px;
  color: #fff;
  text-shadow: -2px 0px 6px #121212;
}
/*Component- Google Sign-in */
.google-button {
  height: 40px;
  border-width: 0;
  background: white;
  color: #737373;
  border-radius: 5px;
  white-space: nowrap;
  box-shadow: 1px 1px 0px 1px rgba(0, 0, 0, 0.05);
  -webkit-transition-property: background-color, box-shadow;
  transition-property: background-color, box-shadow;
  -webkit-transition-duration: 150ms;
          transition-duration: 150ms;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
  padding: 0;
}
.google-button:focus,
.google-button:hover {
  background-color: #e0e0e0;
}
.google-button:active {
  background-color: #e5e5e5;
  box-shadow: none;
  -webkit-transition-duration: 10ms;
          transition-duration: 10ms;
}

.google-button__icon {
  display: inline-block;
  vertical-align: middle;
  margin: 8px 0 8px 8px;
  width: 25px;
  height: 25px;
  box-sizing: border-box;
}

.google-button__icon--plus {
  width: 27px;
}

.google-button__text {
  display: inline-block;
  vertical-align: middle;
  padding: 0 24px;
  font-size: 14px;
  font-weight: bold;
  font-family: "Roboto", arial, sans-serif;
}

/* NavButton function */
.nav-button {
  cursor: pointer;
  color: #fff;
  font-size: 23px;
  margin: 20px 20px;
}

.lds-ellipsis {
  display: inline-block;
  position: absolute;
  width: 64px;
  height: 64px;
  top: 65%;
}
.lds-ellipsis div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 6px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 6px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 26px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 45px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}

.nav-content {
 height: 40vh;
}

.link-content {
  display: flex;
  flex-direction: column;
  height: 25vh;
  justify-content: space-around;
}
